@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,700&display=swap);
.sideBar{
    flex:0.30 1;
    display:flex;
    flex-direction:column;
    align-items:center;
    border-right:1px solid lightgray;
    position:relative;
    overflow:hidden;
}

.sideBar__header{
    height:8%;
    width:100%;
    display:flex;
    justify-content:space-between; 
    align-items:center;
    padding:20px;  
}


.sideBar__header__right{
    width:100px;
    height:100px;
    display:flex;
    justify-content: flex-end;
    align-items:center;
}
.sideBar__header  .MuiSvgIcon-root{
    cursor:pointer;
}
.sidebar__menu-icon-wrapper{
    display:flex;
    justify-content:center;
    align-items:center;
    width:45px;
    height:45px;
    border-radius: 100%;
    padding:20px;
    position:relative;
  
}

.sidebar__menu-icon-wrapper .MuiSvgIcon-root{
    border-radius:100%;
    height:30px;
    width:30px;
    position: absolute;
    left:0px;
    right:0px;
    left:16%;
   
}

.sideBar__header__left .MuiSvgIcon-root{
    font-size:3rem;
}

.sideBar__header__right .MuiSvgIcon-root{
    margin-right:10px;
   
}

.sidebar__search-input-wrapper{
width:100%;
display:flex;
align-items:center;
justify-content:center;
position:relative;
height:65px;
overflow:hidden;
}

.sidebar__search-input-wrapper__iconWrapper{
    justify-content: flex-start;
    align-items:flex-start;
    display:inline;
    position:absolute;
    left:20px;
}

.sidebar__search-input-wrapper .MuiSvgIcon-root{
 /*   position:absolute;*/
    left:20px;
    cursor:pointer;
    
}

.sidebar__search-input{
    width:95%;
    height:70%;
    border-radius: 18px;
    border:0px;
    padding:10px 10px 10px 50px;
   
}
.sidebar__search-input:focus{
        outline: none;
}

.sideBar__chats{
    width:100%;
    height:100%;
    overflow:Auto;
    
}

::-webkit-scrollbar {
    width: 1px;
    border-radius:20%;
  }


.sideBar__add__button{
    display:flex;
    align-items:center;
    border-radius: 50%;
    padding:10px;
    cursor:pointer;
    font-size:5rem;
    border:none;
    
}

.sideBar__header__right__container{
    position:relative;
}
.sideBar__header__right__container__num{
    position:absolute;
    font-size:0.7rem;
    bottom:-10px;
    left:20px;
    font-weight: 700;
}


.chatSnippet{
    display:flex;
    padding:10px;
    align-items:center;
    justify-content:flex-start;
    border-bottom:1px solid lightgray;
    cursor:pointer;
}

.chatSnippet__image{
    border-radius:100%;
    height:50px;
    width:50px;
 
    border:0px;
}

.chatSnippet__body{
    display:flex;
    flex-direction: column;
    margin-left:20px;
    position:relative;
    width:100%;
    
}

.chatSnippet__body__name{
    font-size:1.4rem;
    margin-bottom:2px;
}

.chatSnippet__body__lm{
    font-size:0.8rem;
}

.chatSnippet__body__time{
    position:absolute;
    top:2px;
    right:0px;
    font-size:0.7rem;
}

.addUser__users-list {
  height: 100px;
  border: 1px solid black;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.addUser__users-list__img {
  height: 75px;
  width: 75px;
  border-radius: 100%;
  margin-left: 10px;
  position: relative;
}

.addUser__users-list__name {
  margin-left: 50px;
  align-self: flex-start;
  margin-top: 10px;
}

.addUser__users-list__button {
  position: absolute;
  right: 30px;
}

.adduser__btn {
  background-color: #5995fd;
  border: none;
  outline: none;
  height: 49px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
  transition: 0.5s;
  width: 100%;
  max-width: 380px;
}

.addUser__loader {
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 6px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
  position: "absolute";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  margin-top: 100px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.sidebar__search-input-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 65px;
  overflow: hidden;
}

.sidebar__search-input-wrapper__iconWrapper {
  justify-content: flex-start;
  align-items: flex-start;
  display: inline;
  position: absolute;
  left: 20px;
}

.sidebar__search-input-wrapper .MuiSvgIcon-root {
  /*   position:absolute;*/
  left: 20px;
  cursor: pointer;
}

.sidebar__search-input {
  width: 95%;
  height: 70%;
  border-radius: 18px;
  border: 0px;
  padding: 10px 10px 10px 50px;
}

.sidebar__search-input:focus {
  outline: none;
}
.startConversationDrawer {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 100;
  display: flex;
  flex-direction: column;
}

.startConversationDrawer__header {
  height: 12%;
  display: flex;
  align-items: flex-end;
  padding: 20px;
}
.startConversationDrawer__header h4 {
  font-size: 1.4rem;
}

.startConversationDrawer__header .MuiSvgIcon-root {
  margin-right: 30px;
  height: 25px;
  width: 25px;
  cursor: pointer;
}

.startConversationDrawer__body {
  display: flex;
  height: 88%;
  flex-direction: column;
  justify-content: center;
  overflow: auto;
}

.startConversationDrawer__body__chatList {
  height: 100%;
}

.startConversationDrawer__body__user-info {
  height: 70px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.profileImage {
  height: 55px;
  width: 55px;
  border-radius: 100%;
  margin-left: 10px;
}

.startConversationDrawer__body__user-info__name-and-status {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  position: relative;
}
.startConversationDrawer__body__user-info__name-and-status h5 {
  align-self: flex-start;
  margin-top: 3px;
  margin-left: 10px;
  font-weight: normal;
}

.startConversationDrawer__body__user-info_last-seen {
  position: absolute;
  right: 0;
  top: 0;
  margin-right: 10px;
}
.SidebarDropdown__container {
  width: 150px;
  height: 70px;
  z-index: 50;
  position: absolute;
  display: flex;
  right: 0px;
  top: 0px;
  margin-top: 40px;
  align-items: stretch;
  flex-direction: column;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26), 0 2px 10px 0 rgba(0, 0, 0, 0.16);
}

.SidebarDropdown__container__item {
  margin-top: 10px;
  display: block;
  text-align: center;
  cursor: pointer;
}
.profileDrawer {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 90;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.profileDrawer__header {
  height: 12%;
  display: flex;
  align-items: flex-end;
  padding: 20px;
}
.profileDrawer__header h4 {
  font-size: 1.4rem;
}

.profileDrawer__header .MuiSvgIcon-root {
  margin-right: 30px;
  height: 25px;
  width: 25px;
  cursor: pointer;
}

.profileDrawer__body {
  display: flex;
  height: 88%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.profileDrawer__body__profileImage {
  border-radius: 100%;
  height: 84%;
  width: 84%;
}

.profileDrawer__body .MuiSvgIcon-root {
  height: 100%;
  width: 100%;
}

.profileDrawer__body__image-container {
  position: relative;
  border-radius: 100%;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
}

.profileDrawer__body__image-container__upload-img {
  position: absolute;
  height: 84%;
  width: 84%;
  border-radius: 100%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.profileDrawer__body__image-container__upload-img__text {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.profileDrawer__body__form {
  width: 100%;
}

.inputBox {
  width: 100%;
  height: 100px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  font-weight: 505;
}

.inputBox__outer {
  align-items: center;
  position: relative;
}

.inputBox__input {
  border: none;
  outline: none;
  margin-top: 20px;
  font-size: 1.2rem;
  width: 100%;
}

.inputBox__icon {
  height: 20px !important;
  width: 20px !important;
  position: absolute;
  right: 10px;
  top: 14px;
  cursor: pointer;
}

.inputBox__char-left {
  position: absolute;
  right: 40px;
  top: 14px;
}

.profileDrawer__body__image-container__upload-img__menu {
  position: fixed;
  z-index: 100;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26), 0 2px 10px 0 rgba(0, 0, 0, 0.16);
}
.chatRoom {
  flex: 0.7 1;
  display: flex;
  flex-direction: column;
}

.chatRoom__header {
  height: 8%;
}

.chatRoom__img {
  height: 50px;
  width: 50px;
  border-radius: 100%;
  border: none;
  outline: none;
  overflow: hidden;
}

.chatRoom__header {
  display: flex;
  justify-content: space-between;
}

.chatRoom__header__left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: 20px;
}

.chatRoom__header__left__info {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.chatRoom__header__left__info__name {
  font-weight: 600;
  white-space: nowrap;
}

.chatRoom__header__right {
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.chatRoom__header__right .MuiSvgIcon-root {
  cursor: pointer;
  height: 1.7rem;
  width: 1.7rem;
  margin-right: 20px;
  font-weight: 800;
}

.chatRoom__body {
  height: 85%;
}

.chatRoom__footer {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 7%;
}

.chatRoom__footer__input {
  width: 500px;
  border-radius: 18px;
  height: 70%;
  width: 80%;
  padding-left: 20px;
  border: 0px;
}
.chatRoom__footer__input:focus {
  outline: none;
}

.chatRoom__footer .MuiSvgIcon-root {
  height: 30px;
  width: 30px;
  margin-left: 20px;
  cursor: pointer;
}

.chatRoom__body__chats {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px;
  margin-bottom: 5px;
  overflow: auto;
}

.chatRoom__body__chats__day {
  align-self: center;
  padding: 7px;
  border-radius: 10%;
}

.chatRoom__body__chats__chat {
  padding: 0.5rem;
  margin-bottom: 5px;
  border-radius: 12px 12px 12px 0px;
  display: inline-flex;
  min-width: 200px;
  max-width: 50%;
}

.chatRoom__body__chats__chat_sender {
  padding: 0.5rem;
  margin-bottom: 5px;
  border-radius: 12px 12px 0px 12px;
  display: inline-flex;
  min-width: 200px;
  max-width: 50%;
}

.chatRoom__body__chats__chat-user {
  align-self: flex-end;
}

.chatRoom__body__chats__chat-other-user {
  align-self: flex-start;
}

.chatRoom__body__chats__chat__text {
  white-space: pre-wrap;
  overflow-wrap: break-word;
  min-width: 150px;
}

.chatRoom__body__chats__chat-user__time {
  margin-left: 10px;
  font-size: 0.7rem;
  align-self: flex-end;
}

.chatRoom__body__chats__chat-other-user__time {
  margin-left: 10px;
  font-size: 0.7rem;
  align-self: flex-end;
}
.NotificationBox {
  min-width: 200px;
  height: 50px;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: Center;
  margin-bottom: 10px;
  padding: 20px;
}

.NotificationContainer {
  z-index: 90;
  position: fixed;
  left: 20px;
  bottom: 20px;
  display: flex;
  flex-direction: column;
}
.Intro {
  flex: 0.7 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.Intro__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
}

.Intro__body__text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  height: 30px;
  font-size: 0.9rem;
}

.Intro__body__border {
  height: 2px;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 20px;
}

.Intro__body__extra {
  align-self: flex-start;
}

.Intro__footer {
  width: 100%;
  height: 5px;
  border-top: 5px solid black;
  position: absolute;
  bottom: 0px;
}
* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

.home {
  height: 100vh;
  width: 100vw;
  display: grid;
  place-items: center;
}

.home__body {
  height: 95vh;
  min-width: 600px;
  max-width: 1400px;
  width:100%;
  z-index:20;
  display:flex;
}

.home__header{
  height:127px;
  width:100%;
  z-index:0;
  position:absolute;
}
